<template>
  <div>

    <div class="row" style="height: 100px; background-color: #373737;">
			<div class="col-md-4"></div>
			<div class="col-md-4">
				<div class="row" style="color: white; margin: 10px;">
					<div class="col-md-4">검색 조건</div>
					<div class="col-md-7">검색 단어</div>
					<div class="col-md-1"></div>
				</div>
				<div class="row">
					<div class="col-md-4">
						<select class="form-control" name="key" id="key">
							<option value="name" selected="selected">상품명</option>
							<option value="maker">제조사</option>
							<option value="material">재료명</option>
							<option value="calory">칼로리</option>
						</select>
					</div>
					<div class="col-md-7">
						<input type="text" class="form-control" name="search-input">
					</div>
					<div class="col-md-1">
						<button type="button" name="search-main" class="btn btn-info">검색</button>
					</div>
				</div>
				<div class="row"></div>
			</div>
			<div class="col-md-4"></div>
		</div>

    <tr v-for="food in foods" class="nicecolor" :key="food.code">
      <td><img :src="food.img" @click="show_detail(food.code)"/> </td>
      <td v-html="food.code"></td>
        </tr>

    </div>
</template>

<script>
import http from "../http-common";
export default {
  name: "board-list",
  data() {
    return {
      upHere: false,
      foods: [],
      loading: true,
      errored: false
    };
  },
  methods: {
    retrieveBoard() {
      http
        .get("rest/food/SearchAll")
        .then(response => (this.foods = response.data))
        .catch(() => {
          this.errored = true;
        })
        .finally(() => (this.loading = false));
    },
    refreshList() {
      this.retrieveBoard();
    },
    show_detail: function(boardnum) {
      alert(boardnum + "상세보기");
      this.$router.push("/detailboard/" + boardnum);
    },
    update_board: function(boardnum) {
      alert(boardnum + "수정하기");
      this.$router.push("/updateboard/" + boardnum);
    },
    delete_board(boardnum) {
      alert(boardnum + "게시판을 삭제합니다.");
      http
        .delete("/deleteBoard/" + boardnum)
        .then(response => {
          if (response.data.state == null) {
            alert("삭제처리를 하였습니다.");
            this.refreshList();
          } else {
            this.refreshList();
            alert("삭제처리를 하지 못했습니다.");
          }
        })
        .catch(() => {
          this.errored = true;
        })
        .finally(() => (this.loading = false));
    }
  },
  filters: {
    salarydecimal(value) {
      var a = parseInt(value);
      return a.toFixed(2);
    }
  },
  mounted() {
    this.refreshList();
  }
};
</script>
